:root {
  --black: rgba(20, 20, 20, 1);
  --dark-green: rgba(30, 40, 39, 1);
  --white: rgba(255, 255, 255, 1);
}

body {
  font-family: 'Epilogue', sans-serif;
  color: var(--dark-green); 
  background-color: var(--white);
}

h1,
h2,
h3,
p {
  margin-block-start: 0;
  margin-block-end: 0;
}

a {
  text-decoration: none;
  font-style: normal;
}

ul {
  padding-inline-start: 0;
  margin-block-start: 0;
  margin-block-end: 0;
  list-style-type: none;
}

.container {
  width: 345px;
  margin: 0 auto;
  padding: 0 15px;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.index {
  color: green;
  background-color: lightgreen;
